body {
	color: $color;
	font-size: $font-size;
	font-weight: $font-weight;
	font-family: $font-family;
	line-height: $line-height;
	background: #F9F9F9;

	&.hide-scroll {
		overflow: hidden;
	}
}

//       (position: fixed)     (  )
//   (       )  
//   (     (position: fixed))
html.ios-fixed-filter {
	height: 100%;
}

html.ios-fixed-filter,
html.ios-fixed-filter body {
	-webkit-overflow-scrolling: touch;
	overflow: auto;
}
// 

a {
	color: $colorAccent;
}

img {
	max-width: 100%;
}